<template>
  <div id="httpClientDemo">
    <el-button @click="doIt">doIt</el-button>
    <div v-html="rawHtml"></div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      rawHtml: ''
    }
  },
  methods: {
    doIt: function () {
      fetch('/api/HttpClient/doIt').then(response => {
        return response.text()
      }).then(htmlDate => {
        this.rawHtml = htmlDate
      }).catch(err => console.log('err: ' + err))
    }
  }
}
</script>




