<template>
  <div id="updateUser">
    <el-table :data="Users" border>
      <el-table-column prop="name" label="Name"></el-table-column>
      <el-table-column prop="age" label="Age"></el-table-column>
      <el-table-column prop="sex" label="Sex"></el-table-column>
      <el-table-column prop="ranking" label="Ranking" width="120px;"></el-table-column>
      <el-table-column label="option">
        <template scope="scope">
          <el-button @click="ChangeInfo(scope.$index)" type="warning" size="small">ChangeInfo</el-button>

          <el-dialog title="Change User Information" :visible.sync="ChangeInfoFormVisible">

            <div class="ChangeForm">
            <el-form :model="ChangeInfoForm" label-width="90px">
              <el-form-item prop="name" label="Name: ">
                <el-input v-model="ChangeInfoForm.name"></el-input>
              </el-form-item>
              <el-form-item prop="sex" label="Sex: ">
                <el-select v-model="ChangeInfoForm.sex" placeholder="Please Select">
                  <el-option
                  v-for="sex in Sexs"
                  :key="sex.value"
                  :label="sex.lable"
                  :value="sex.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item prop="age" label="Age: ">
                <el-input-number v-model="ChangeInfoForm.age"></el-input-number>
              </el-form-item>
              <el-form-item prop="ranking" label="Ranking: ">
                <el-input-number v-model="ChangeInfoForm.ranking"></el-input-number>
              </el-form-item>
            </el-form>
            </div>

            <div slot="footer" class="dialog-footer">
              <el-button type="info" @click="ChangeInfoFormVisible = false">Cancel</el-button>
              <el-button type="danger" @click="Change">Change</el-button>
            </div>
          </el-dialog>

          <el-button @click.native.prevent="deleteUser(scope.$index)" type="danger" size="small">Delete</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      Sexs: [
        {
          value: 'Male',
          label: 'Male'
        }, {
          value: 'Female',
          label: 'Female'
        }],
      Users: [],
      ChangeInfoFormVisible: false,
      ChangeInfoForm: {
        id: '',
        name: '',
        sex: '',
        age: 0,
        ranking: 0
      }
    }
  },
  methods: {
    ChangeInfo: function (index) {
      this.ChangeInfoForm = this.Users[index]
      this.ChangeInfoFormVisible = true
    },
    Change: function () {
      let formDate = new FormData()
      formDate.append('context', JSON.stringify(this.ChangeInfoForm))
      fetch('/Vue/UpdateUser', {
        method: 'POST',
        body: formDate
      }).then((response) => {
        if (response.ok) {
          return response.json()
        } else {
          this.$notify.error({
            title: '连接失败',
            message: '与服务器连接失败，请重试！'
          })
        }
      }).then(result => {
        if (result.status === 'success') {
          this.$notify.info({
            title: '修改结果',
            message: '修改成功'
          })
        } else {
          this.$notify.error({
            title: '修改结果',
            message: '修改失败'
          })
        }
      })
      this.ChangeInfoFormVisible = false
    },
    deleteUser: function (index) {
      this.$confirm('This option will delete a user! Are you sure???', 'Remind', {
        cancelButtonText: 'Cancel',
        confirmButtonText: 'Sure',
        type: 'warning'
      }).then(() => {
        let formDate = new FormData()
        formDate.append('context', JSON.stringify(this.Users[index]))
        fetch('/Vue/deleteUser', {
          method: 'POST',
          body: formDate
        }).then(response => {
          if (response.ok) {
            return response.json()
          } else {
            this.$notify.info({
              title: '连接出错',
              message: '与服务器的连接失败，请重试！'
            })
          }
        }).then(result => {
          if (result.status === 'success') {
            this.Users.splice(index, 1)
            this.$notify.success({
              title: '删除结果',
              message: '删除成功'
            })
          } else {
            this.$notify.error({
              title: '删除结果',
              message: '删除失败'
            })
          }
        }).catch(err => console.error('err:' + err))
      }).catch(() => {
        this.$message('已取消删除')
      })
    }
  },
  mounted: function () {
    fetch('/Vue/searchAll').then(
      response => {
        if (response.status === 200) {
          return response.json()
        } else {
          console.log('error:' + response.status)
        }
      }).then(
      jsonData => {
        this.Users = jsonData.data
      }).catch(err => console.log('error:' + err))
  }
}
</script>

<style scoped>
.ChangeForm{
  width: 270px;
}
</style>




