<template>
	<div id="createUser">
	<div id="create">
	<el-form ref="ruleForm" :model="newUser"  :rules="rules" label-width="80px">
		<el-form-item label="Name: " prop="name">
			<el-input v-model="newUser.name"></el-input>
		</el-form-item>
    <el-form-item label="Sex: " prop="sex">
			<el-select v-model="newUser.sex" placeholder="请选择">
				<el-option
				v-for="sex in Sexs"
				:key="sex.value"
				:label="sex.label"
				:value="sex.value">
				</el-option>
			</el-select>
		</el-form-item>
		<el-form-item label="Age: " prop="age">
			<el-input-number v-model="newUser.age"></el-input-number>
		</el-form-item>
    <el-form-item label="Ranking: " prop="ranking">
      <el-input-number v-model="newUser.ranking"></el-input-number>
    </el-form-item>
	</el-form>
  <el-button @click="Create">Create A new User</el-button>
	</div>
	<el-table  id="UsersTable" :data="Users" border>
		<el-table-column prop="name" label="Name"></el-table-column>
		<el-table-column prop="age" label="Age"></el-table-column>
		<el-table-column prop="sex" label="Sex"></el-table-column>
    <el-table-column prop="ranking" label="Ranking"></el-table-column>
	</el-table>
	</div>
</template>

<script>
export default {
  data () {
    return {
      newUser: {
        name: '',
        sex: '',
        age: 0,
        ranking: 0
      },
      rules: {
        name: [
          { required: true, message: '请输入姓名', trigger: 'blur' }
        ],
        sex: [
          { required: true, message: '请选择性别', trigger: 'blur' }
        ]},
      Sexs: [
        {
          value: 'Male',
          label: 'Male'
        }, {
          value: 'Female',
          label: 'Female'
        }],
      Users: []
    }
  },
  methods: {
    Create: function () {
      this.$refs['ruleForm'].validate((valid) => {
        if (valid) {
          let formDate = new FormData()
          formDate.append('context', JSON.stringify(this.newUser))
          fetch('/Vue/addOneUser', {
            method: 'POST',
            body: formDate
          }).then(response => {
            if (response.ok) {
              return response.json()
            } else {
              this.$notify.info({
                title: '连接出错',
                message: '与服务器的连接失败，请重试！'
              })
            }
          }).then(result => {
            if (result.status === 'success') {
              this.Users.push(this.newUser)
              this.newUser = {
                name: '',
                sex: '',
                age: 0,
                ranking: 0
              }
              this.$notify.success({
                title: '添加结果',
                message: '添加成功'
              })
            } else {
              this.$notify.error({
                title: '添加结果',
                message: '添加失败'
              })
            }
          }).catch(err => console.log('err:' + err))
        } else {
          alert('请检查是否填写完整！')
        }
      })
    }
  },
  mounted: function () {
    fetch('/Vue/searchAll').then(
      response => {
        if (response.status === 200) {
          return response.json()
        } else {
          console.log('error:' + response.status)
        }
      }).then(
      jsonData => {
        this.Users = jsonData.data
      }).catch(err => console.log('error:' + err))
  }
}
</script>

<style scope>
#UsersTable{
  margin-top: 9px;
}
#create{
  width: 300px;
}
</style>



