<template>
	<div id="searchUser">
    <el-collapse accordion>
      <el-collapse-item>
        <template slot="title">
          Precise Search <li class="el-icon-search"></li>
        </template>
        <el-col :span="9">
          <el-form :model="preciseSearchForm" label-width="80px">
            <el-form-item label="Name：">
              <el-input v-model="preciseSearchForm.name"></el-input>
            </el-form-item>
            <el-button @click="bt_preciseSearch" type="primary" style="float: right">Search</el-button>
          </el-form>
        </el-col>
      </el-collapse-item>
      <el-collapse-item>
        <template slot="title">
          Fuzzy Search <li class="el-icon-search"></li>
        </template>
        <el-col :span="9">
          <el-form :model="fuzzySearchForm" label-width="80px">
            <el-form-item label="Name：">
              <el-input v-model="fuzzySearchForm.name"></el-input>
            </el-form-item>
            <el-button @click="bt_fuzzySearch" type="primary" style="float: right">Search</el-button>
          </el-form>
        </el-col>
      </el-collapse-item>
      <el-collapse-item>
        <template slot="title">
          Conditions Search <li class="el-icon-search"></li>
        </template>
        <el-col :span="9">
          <el-form :model="conditionsSearchForm" style="width:300px;margin: 0 auto;" label-width="126px">
            <el-form-item label="Name：">
              <el-input v-model="conditionsSearchForm.name"></el-input>
            </el-form-item>
            <el-form-item label="Sex：" prop="sex">
              <el-select v-model="conditionsSearchForm.sex" >
                <el-option
                v-for="sex in Sexs"
                :key="sex.value"
                :label="sex.label"
                :value="sex.value">
              </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="Age Older Than：">
              <el-input-number v-model="conditionsSearchForm.age"></el-input-number>
            </el-form-item>
            <el-button @click="bt_ResetConditions" type="success">Reset Conditions</el-button>
            <el-button @click="bt_conditionsSearch" type="primary" style="float: right">Search</el-button>
          </el-form>
        </el-col>
      </el-collapse-item>
    </el-collapse>
  <div id="searchResult">
    <el-table :data="searchResult" border>
      <el-table-column prop="name" label="Name" ></el-table-column>
      <el-table-column prop="age" label="Age"></el-table-column>
      <el-table-column prop="sex" label="Sex"></el-table-column>
      <el-table-column prop="ranking" label="Ranking"></el-table-column>
    </el-table>
    </div>
	</div>
</template>

<script>
export default {
  data () {
    return {
      Sexs: [
        {
          value: 'Male',
          label: 'Male'
        }, {
          value: 'Female',
          label: 'Female'
        }],
      searchResult: [],
      preciseSearchForm: {
        name: ''
      },
      fuzzySearchForm: {
        name: ''
      },
      conditionsSearchForm: {
        name: '',
        sex: '',
        age: '',
        ranking: ''
      }
    }
  },
  methods: {
    bt_ResetConditions: function () {
      this.conditionsSearchForm = {
        name: '',
        sex: '',
        age: '',
        ranking: ''
      }
    },
    bt_preciseSearch: function () {
      let formDate = new FormData()
      formDate.append('context', JSON.stringify(this.preciseSearchForm))
      fetch('/Vue/preciseSearchByName', {
        method: 'POST',
        body: formDate
      }).then(response => {
        if (response.ok) {
          return response.json()
        } else {
          this.$notify.info({
            title: '连接结果',
            message: '与服务器的连接失败，请重试！'
          })
        }
      }).then(result => {
        if (result.status === 'fail') {
          this.$notify.info({
            title: '查询结果',
            message: '没有符合查询条件的个人信息！'
          })
        } else {
          this.searchResult = new Array(result.data)
        }
      }).catch(err => console.log('err:' + err))
    },
    bt_fuzzySearch: function () {
      let formDate = new FormData()
      formDate.append('context', JSON.stringify(this.fuzzySearchForm))
      fetch('/Vue/fuzzySearchByName', {
        method: 'POST',
        body: formDate
      }).then(response => {
        if (response.ok) {
          return response.json()
        } else {
          this.$notify.info({
            title: '连接失败',
            message: '与服务器连接失败，请重试！'
          })
        }
      }).then(result => {
        if (result.status === 'fail') {
          this.$notify.info({
            title: '查询结果',
            message: '没有符合查询条件的个人信息！'
          })
        } else {
          this.searchResult = result.data
        }
      }).catch(err => console.log('err:' + err))
    },
    bt_conditionsSearch: function () {
      let formDate = new FormData()
      formDate.append('context', JSON.stringify(this.conditionsSearchForm))
      fetch('/Vue/conditionsSearch', {
        method: 'POST',
        body: formDate
      }).then(response => {
        if (response.ok) {
          return response.json()
        } else {
          this.$notify.info({
            title: '连接结果',
            message: '与服务器的连接失败，请重试！'
          })
        }
      }).then(result => {
        if (result.status === 'fail') {
          this.$notify.info({
            title: '查询结果',
            message: '没有符合查询条件的个人信息！'
          })
        } else {
          this.searchResult = result.data
        }
      }).catch(err => console.log('err:' + err))
    }
  },
  mounted: function () {
  }
}
</script>

<style scoped>

</style>



